<template>
  <el-dialog
    title=""
    :visible="visible"
    @close="$emit('close')"
    width="500px"
  >
    <el-form ref="form" :model="localFormData" :rules="rules" label-width="100px">
      <!-- name  sourceName -->
      <el-form-item label="" prop="sourceName">
        <el-input v-model="localFormData.sourceName"></el-input>
      </el-form-item>
      <el-form-item label="" prop="sourceType">
        <el-select v-model="localFormData.sourceType" placeholder="">
          <el-option label="" value="GROUNDWATER"></el-option>
          <el-option label="" value="SURFACE_WATER"></el-option>
          <el-option label="" value="RESERVOIR"></el-option>
          <el-option label="" value="RIVER"></el-option>
          <el-option label="" value="LAKE"></el-option>
          <el-option label="" value="DRINKING_WATER"></el-option>
          <el-option label="" value="INDUSTRIAL_WATER"></el-option>
          <el-option label="" value="AGRICULTURAL_WATER"></el-option>
          <el-option label="" value="SEWAGE_TREATMENT_PLANT"></el-option>
          <el-option label="" value="WELL_WATER"></el-option>
          <el-option label="" value="SPRING_WATER"></el-option>
   
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="description">
        <el-input type="textarea" v-model="localFormData.description"></el-input>
      </el-form-item>
      <el-form-item label="" prop="isActive" v-if="isEdit">
        <el-switch v-model="localFormData.isActive"></el-switch>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="$emit('close')"></el-button>
      <el-button type="primary" @click="handleSubmit"></el-button>
    </div>
  </el-dialog>
</template>

<script>
export default {
  props: {
    visible: { type: Boolean, default: false },
    formData: { type: Object, default: () => ({}) },
    isEdit: { type: Boolean, default: false }
  },
  data() {
    return {
      localFormData: {},
      rules: {
        //  name  sourceName
        sourceName: [
          { required: true, message: '', trigger: 'blur' }
        ],
        sourceType: [
          { required: true, message: '', trigger: 'change' }
        ]
      }
    }
  },
  watch: {
    formData: {
      immediate: true,
      handler(newVal) {
        this.localFormData = JSON.parse(JSON.stringify(newVal));
      }
    }
  },
  methods: {
    handleSubmit() {
      this.$refs.form.validate((valid) => {
        if (valid) {
          this.$emit('save', this.localFormData);
        }
      });
    }
  }
}
</script>