<template>
    <div class="app-container">
         <MainHeader />
      <!--  -->
      <header class="system-header">
        <div class="container">
          <div class="header-right">
   
          </div>
        </div>
      </header>
  
      <!--  -->
      <nav class="module-nav">
        <div class="container">
          <button 
            class="nav-btn" 
            :class="{ active: activeModule === 'private-report' }"
            @click="switchModule('private-report')"
          >
            <i class="fa fa-file-text-o nav-icon"></i> 
          </button>
          <button 
            class="nav-btn" 
            :class="{ active: activeModule === 'public-report' }"
            @click="switchModule('public-report')"
          >
            <i class="fa fa-qrcode nav-icon"></i> 
          </button>
        </div>
      </nav>
  
      <!--  -->
      <main class="container main-content">
        <!--  -->
        <section v-show="activeModule === 'private-report'" class="module-panel">
          <!--  -->
          <div class="header-section">
            <div class="header-content">
              <div class="title-section">
                <h1 class="main-title"></h1>
                <p class="sub-title"></p>
              </div>
            </div>
            
            <!--  -->
            <div class="feature-cards-horizontal">
              <div class="feature-card" @click="showGenerateModal = true">
                <div class="feature-icon">
                  <i class="fa fa-plus"></i>
                </div>
                <div class="feature-content">
                  <h3></h3>
                  <p></p>
                </div>
                <div class="feature-arrow">
                  <i class="fa fa-arrow-right"></i>
                </div>
              </div>

            </div>
          </div>
  
          <!--  -->
          <div class="toolbar-section">
            <div class="search-box">
              <el-input
                v-model="searchSourceId"
                placeholder="ID..."
                prefix-icon="el-icon-search"
                class="search-input"
                clearable
                @clear="handleSearchClear"
                @keyup.enter="handleSearchBySourceId"
              ></el-input>
              <el-button 
                type="primary" 
                @click="handleSearchBySourceId"
                :loading="searchLoading"
                class="search-btn"
              >
                
              </el-button>
            </div>
          </div>
  
          <!--  -->
          <div class="reports-grid">
            <div 
              v-for="report in filteredReports" 
              :key="report.strId" 
              class="report-card"
            >
              <div class="report-header">
                <div class="report-title-section">
                  <h3 class="report-title"></h3>
                </div>
              </div>
              
              <div class="report-content">
                <div class="report-meta">
                  <div class="meta-item">
                    <i class="fa fa-file-text-o meta-icon"></i>
                    <span class="meta-label">ID:</span>
                    <span class="meta-value copyable-id" @click="copyReportId(report.strId)">
                      {{ report.strId }}
                      <i class="fa fa-copy copy-icon"></i>
                    </span>
                  </div>
                  <div class="meta-item">
                    <i class="fa fa-clock-o meta-icon"></i>
                    <span class="meta-label">:</span>
                    <span class="meta-value">{{ formatDateTime(report.generatedAt) }}</span>
                  </div>
                  <div v-if="report.keywords" class="meta-item">
                    <i class="fa fa-tags meta-icon"></i>
                    <span class="meta-label">:</span>
                    <span class="meta-value">{{ report.keywords }}</span>
                  </div>
                </div>
              </div>
              
              <div class="report-actions">
                <el-button 
                  @click="viewPrivateReport(report)" 
                  type="text" 
                  size="small"
                  class="btn-action view"
                >
                
                  <i class="fa fa-eye"></i>
                  
                </el-button>
                <el-button 
                  @click="togglePublishStatus(report)" 
                  type="text" 
                  size="small"
                  :class="['btn-action', report.isPublished ? 'unpublish' : 'publish']"
                >
                  <i :class="report.isPublished ? 'fa fa-check' : 'fa fa-share'"></i>
                  {{ report.isPublished ? '' : '' }}
                </el-button>
                <el-button 
                  @click="deleteReport(report.strId)" 
                  type="text" 
                  size="small"
                  class="btn-action delete"
                >
                  <i class="fa fa-trash"></i>
                  
                </el-button>
              </div>
            </div>
  
            <!--  -->
            <div v-if="filteredReports.length === 0 && !loading.privateReports" class="empty-state">
              <div class="empty-icon-box">
                <i class="fa fa-file-text-o empty-icon"></i>
              </div>
              <p class="empty-tip"></p>
              <p class="empty-subtip">""</p>
            </div>
          </div>
  
          <!--  -->
          <div v-if="loading.privateReports" class="loading-state">
            <i class="fa fa-spinner fa-spin loading-icon"></i>
            <p>...</p>
          </div>
        </section>
  
        <!--  -->
        <section v-show="activeModule === 'public-report'" class="module-panel">
          <div class="panel-header">
            <h2 class="panel-title">
              <i class="fa fa-qrcode panel-icon"></i> 
            </h2>
            <div class="public-search">
              <div class="form-input-group public-search-input">
                <i class="fa fa-search input-icon"></i>
                <input 
                  type="text" 
                  v-model="publicReportId" 
                  placeholder="ID"
                  @keyup.enter="queryPublicReport"
                >
              </div>
              <button class="btn primary-btn" @click="queryPublicReport" :disabled="loading.publicReport">
                <i class="fa fa-qrcode"></i> 
                
              </button>
            </div>
          </div>
  
          <!--  -->
          <div class="public-report-detail">
            <div v-if="!publicReportDetail && !loading.publicReport" class="empty-state">
              <div class="empty-icon-box">
                <i class="fa fa-file-o empty-icon"></i>
              </div>
              <p class="empty-tip">ID</p>
            </div>
  
            <div v-else-if="loading.publicReport" class="loading-state">
              <i class="fa fa-spinner fa-spin loading-icon"></i>
              <p>...</p>
            </div>
  
            <div v-else-if="publicReportDetail" class="report-content">
              <h3><i class="fa fa-file-text-o"></i> </h3>
              <div class="report-info">
                <span><i class="fa fa-file-text-o"></i> ID
                  <span class="copyable-id" @click="copyReportId(publicReportDetail.strId)">
                    {{ publicReportDetail.strId }}
                    <i class="fa fa-copy copy-icon"></i>
                  </span>
                </span>
                <span><i class="fa fa-tint"></i> {{ publicReportDetail.sourceName || '' }}</span>
                <span><i class="fa fa-tag"></i> {{ publicReportDetail.sourceType || '' }}</span>
                <span><i class="fa fa-calendar"></i> {{ formatDateTime(publicReportDetail.generatedAt) }}</span>
                <span v-if="publicReportDetail.keywords"><i class="fa fa-tags"></i> {{ publicReportDetail.keywords }}</span>
              </div>
              <div class="report-text">
                <h4></h4>
                <div class="formatted-report-content" v-html="formatReportContent(publicReportDetail.reportContent)"></div>
              </div>
              <div v-if="publicReportDetail.qrCodePath" class="qr-code-section">
                <h4></h4>
                 <img :src="getQrCodeUrl(publicReportDetail.qrCodePath)" alt="" class="qr-code-image">
              </div>
            </div>
          </div>
        </section>
      </main>
  
      <!--  -->
      <div v-if="showGenerateModal" class="modal">
        <div class="modal-overlay" @click="showGenerateModal = false"></div>
        <div class="modal-content">
          <div class="modal-header">
            <h3 class="modal-title">
              <i class="fa fa-file-text modal-icon"></i> 
            </h3>
            <button class="close-modal" @click="showGenerateModal = false">&times;</button>
          </div>
          <div class="modal-body">
            <!--  -->
            <form @submit.prevent="generateReport">
              <div class="form-item">
                <label class="form-label">ID <span class="required">*</span></label>
                <div class="form-input-group">
                  <i class="fa fa-tint input-icon"></i>
                  <input 
                    type="text" 
                    v-model="generateForm.sourceStrId" 
                    required 
                    placeholder="01KAR9BBFPNCV0KGVDHPQ941A7"
                  >
                </div>
              </div>
            </form>
          </div>
          <div class="modal-footer">
            <button class="btn cancel-btn" @click="showGenerateModal = false"></button>
            <button class="btn primary-btn" @click="generateReport" :disabled="loading.generateReport">
              {{ loading.generateReport ? '...' : '' }}
            </button>
          </div>
        </div>
      </div>

      <!--  -->
<div v-if="showReportDetailModal" class="modal">
  <div class="modal-overlay" @click="showReportDetailModal = false"></div>
  <div class="modal-content" style="max-width: 800px;">
    <div class="modal-header">
      <h3 class="modal-title">
        <i class="fa fa-file-text-o modal-icon"></i> 
      </h3>
      <button class="close-modal" @click="showReportDetailModal = false">&times;</button>
    </div>
    <div class="modal-body">
      <div v-if="currentReportDetail" class="report-detail-content">
        <div class="detail-section">
          <h4></h4>
          <div class="detail-grid">
            <div class="detail-item">
              <span class="detail-label">ID:</span>
              <span class="detail-value">{{ currentReportDetail.sourceStrId }}</span>
            </div>
            <div class="detail-item">
              <span class="detail-label">:</span>
              <span class="detail-value">{{ currentReportDetail.sourceName || '' }}</span>
            </div>
            <div class="detail-item">
              <span class="detail-label">:</span>
              <span class="detail-value">{{ currentReportDetail.sourceType || '' }}</span>
            </div>
            <div class="detail-item">
              <span class="detail-label">:</span>
              <span class="detail-value">{{ formatDateTime(currentReportDetail.generatedAt) }}</span>
            </div>
            <div class="detail-item">
              <span class="detail-label">:</span>
              <span class="detail-value">{{ currentReportDetail.isPublished ? '' : '' }}</span>
            </div>
            <div v-if="currentReportDetail.keywords" class="detail-item full-width">
              <span class="detail-label">:</span>
              <span class="detail-value">{{ currentReportDetail.keywords }}</span>
            </div>
          </div>
        </div>

        <div class="detail-section" v-if="currentReportDetail.usedData && currentReportDetail.usedData.length > 0">
          <h4></h4>
          <div class="data-table">
            <div class="table-header">
              <div class="table-cell">ID</div>
              <div class="table-cell"></div>
              <div class="table-cell"></div>
              <div class="table-cell"></div>
            </div>
            <div class="table-row" v-for="data in currentReportDetail.usedData" :key="data.strId">
              <div class="table-cell">{{ data.strId }}</div>
              <div class="table-cell">{{ formatDateTime(data.measuredAt) }}</div>
              <div class="table-cell">{{ data.turbidityValue }}</div>
              <div class="table-cell">{{ data.unit }}</div>
            </div>
          </div>
        </div>

        <div class="detail-section">
          <h4></h4>
          <div class="formatted-report-content" v-html="formatReportContent(currentReportDetail.reportContent)"></div>
        </div>
      </div>
      <div v-else class="loading-state">
        <i class="fa fa-spinner fa-spin loading-icon"></i>
        <p>...</p>
      </div>
    </div>
    <div class="modal-footer">
      <button class="btn primary-btn" @click="showReportDetailModal = false"></button>
    </div>
  </div>
</div>
      <footer class="system-footer">
        <div class="container">
          <p class="footer-text"> 2024 </p>
        </div>
      </footer>
    </div>
  </template>
  
  <script>
  import axios from 'axios';
  // 
import MainHeader from '../components/MainHeader.vue'
  export default {
    name: 'ReportManagement',
  components: {
    MainHeader  // 
  },
    data() {
      return {
        activeModule: 'private-report',
        currentUser: {
          username: '',
          id: 'user_001'
        },
        
        // 
        privateReports: [],
        filteredReports: [],
        showGenerateModal: false,
        generateForm: {
          sourceStrId: ''
        },
        
        // 
        searchSourceId: '',
        searchLoading: false,
        isSearching: false,
        
        // 
        publicReportId: '',
        publicReportDetail: null,
        
        // 
        showReportDetailModal: false,
        currentReportDetail: null,
        
        // 
        loading: {
          privateReports: false,
          generateReport: false,
          publicReport: false,
          reportDetail: false
        }
      };
    },
    mounted() {
      console.log('...');
      this.fetchPrivateReports();
    },
    methods: {
      // 
      switchModule(module) {
        this.activeModule = module;
        if (module === 'private-report') {
          this.fetchPrivateReports();
        } else if (module === 'public-report') {
          this.publicReportDetail = null;
          this.publicReportId = '';
        }
      },

      // HTML
      formatReportContent(content) {
        if (!content || typeof content !== 'string') {
          return '<div class="no-content"></div>';
        }
        
        // <think>
        let filteredContent = content.replace(/<think>[\s\S]*?<\/think>/gi, '');
        
        // 
        filteredContent = filteredContent
          .replace(/\n\s*\n\s*\n/g, '\n\n')
          .trim();
        
        // 
        if (!filteredContent.trim()) {
          return '<div class="no-content"></div>';
        }
        
        // Markdown
        let formattedContent = filteredContent;
        
        // 
        formattedContent = formattedContent.replace(/^\*\* (.+?) \*\*/gm, '<h2 class="report-section-title">$1</h2>');
        formattedContent = formattedContent.replace(/^### (.+?)$/gm, '<h3 class="report-subtitle">$1</h3>');
        
        // 
        formattedContent = formattedContent.replace(/^- (.+?)$/gm, '<li class="report-list-item">$1</li>');
        
        // ul
        if (formattedContent.includes('<li class="report-list-item">')) {
          formattedContent = formattedContent.replace(/(<li class="report-list-item">[\s\S]*?)<\/li>/g, '<ul class="report-list">$1</ul>');
        }
        
        // 
        formattedContent = formattedContent.replace(/\| (.+?) \|/g, (match, content) => {
          const cells = content.split('|').map(cell => cell.trim());
          if (cells.length > 1) {
            // 
            if (cells.every(cell => cell.replace(/-/g, '') === '')) {
              return '';
            }
            return `<tr class="report-table-row">${cells.map(cell => `<td class="report-table-cell">${cell}</td>`).join('')}</tr>`;
          }
          return match;
        });
        
        // table
        if (formattedContent.includes('<tr class="report-table-row">')) {
          formattedContent = formattedContent.replace(/(<tr class="report-table-row">[\s\S]*?)<\/tr>/g, '<table class="report-table">$1</table>');
        }
        
        // 
        formattedContent = formattedContent.replace(/\*\*(.+?)\*\*/g, '<strong class="report-bold">$1</strong>');
        
        // 
        formattedContent = formattedContent.replace(/^-{3,}$/gm, '<hr class="report-divider">');
        
        //  - 
        const lines = formattedContent.split('\n');
        let inParagraph = false;
        let result = '';
        
        for (let i = 0; i < lines.length; i++) {
          const line = lines[i].trim();
          
          if (!line) {
            if (inParagraph) {
              result += '</p>';
              inParagraph = false;
            }
            result += '<br>';
          } else if (line.startsWith('<h2') || line.startsWith('<h3') || line.startsWith('<ul') || 
                     line.startsWith('<table') || line.startsWith('<li') || line.startsWith('<hr')) {
            if (inParagraph) {
              result += '</p>';
              inParagraph = false;
            }
            result += line + '\n';
          } else {
            if (!inParagraph) {
              result += '<p class="report-paragraph">';
              inParagraph = true;
            }
            result += line + ' ';
          }
        }
        
        // 
        if (inParagraph) {
          result += '</p>';
        }
        
        return result || '<div class="no-content"></div>';
      },

      // <think>
      filterReportContent(content) {
        if (!content || typeof content !== 'string') {
          return content || '';
        }
        
        // <think>
        const thinkTagRegex = /<think>[\s\S]*?<\/think>/gi;
        
        // <think>
        let filteredContent = content.replace(thinkTagRegex, '');
        
        // 
        filteredContent = filteredContent
          .replace(/\n\s*\n\s*\n/g, '\n\n')
          .trim();
        
        return filteredContent;
      },
  
      // 
      fetchPrivateReports() {
        this.loading.privateReports = true;
        console.log('...');
        
        axios.get('/reports')
          .then(response => {
            console.log('API:', response.data);
            
            if (response.data && Array.isArray(response.data)) {
              console.log(':', response.data.length);
              
              // 
              this.privateReports = response.data.map(report => ({
                strId: report.strId,
                sourceStrId: report.sourceStrId,
                generatedAt: report.generatedAt,
                isPublished: report.isPublished, //  isPublished
                keywords: report.keywords,
                qrCodePath: report.qrCodePath,
                reportContent: this.filterReportContent(report.reportContent) // 
              }));
              
              this.filteredReports = [...this.privateReports];
              console.log(':', this.privateReports);
            } else {
              console.log('');
              this.privateReports = [];
              this.filteredReports = [];
            }
          })
          .catch(error => {
            console.error(':', error);
            this.showToast('', 'error');
            this.privateReports = [];
            this.filteredReports = [];
          })
          .finally(() => {
            this.loading.privateReports = false;
            console.log(':', this.privateReports.length);
          });
      },
  
      // ID
      async handleSearchBySourceId() {
        if (!this.searchSourceId.trim()) {
          this.$message.warning('ID')
          return
        }
        
        this.searchLoading = true
        this.isSearching = true
        try {
          const response = await axios.get(`/reports/by-source/${this.searchSourceId.trim()}`)
          
          console.log(':', response)
          
          let reports = []
          if (response.data && Array.isArray(response.data)) {
            reports = response.data.map(report => ({
              strId: report.strId,
              sourceStrId: report.sourceStrId,
              generatedAt: report.generatedAt,
              isPublished: report.isPublished, //  isPublished
              keywords: report.keywords,
              qrCodePath: report.qrCodePath,
              reportContent: this.filterReportContent(report.reportContent) // 
            }))
          }
          
          if (reports.length > 0) {
            this.filteredReports = reports
            this.$message.success(` ${reports.length} `)
          } else {
            this.filteredReports = []
            this.$message.warning('')
            setTimeout(() => {
              this.handleSearchClear()
            }, 1500)
          }
        } catch (error) {
          console.error(':', error)
          this.filteredReports = []
          if (error.response && error.response.status === 404) {
            this.$message.warning('ID')
            setTimeout(() => {
              this.handleSearchClear()
            }, 1500)
          } else {
            this.$message.error('')
          }
        } finally {
          this.searchLoading = false
        }
      },
  
      // 
      handleSearchClear() {
        this.isSearching = false
        this.searchSourceId = ''
        this.searchLoading = false
        this.filteredReports = [...this.privateReports]
        this.$message.info('')
      },
      // 
      generateReport() {
        if (!this.generateForm.sourceStrId.trim()) {
          this.showToast('ID', 'error');
          return;
        }
  
        this.loading.generateReport = true;
  
        const requestData = {
          sourceStrId: this.generateForm.sourceStrId
        };
  
        axios.post('/reports/generate', requestData)
          .then((response) => {
            // 
            if (response.data && response.data.data) {
              const newReport = {
                strId: response.data.data.strId,
                sourceStrId: response.data.data.sourceStrId,
                generatedAt: response.data.data.generatedAt,
                isPublished: response.data.data.isPublished, //  isPublished
                keywords: response.data.data.keywords,
                qrCodePath: response.data.data.qrCodePath,
                reportContent: this.filterReportContent(response.data.data.reportContent), // 
                message: response.data.message || ''
              };
              
              // 
              this.privateReports.unshift(newReport);
              this.filteredReports = [...this.privateReports];
            }
            
            this.showToast('', 'success');
            this.showGenerateModal = false;
            this.resetGenerateForm();
          })
          .catch(error => {
            console.error(':', error);
            this.showToast(`: ${error.response?.data?.message || ''}`, 'error');
          })
          .finally(() => {
            this.loading.generateReport = false;
          });
      },
  
      // 
      viewPrivateReport(report) {
        this.loading.reportDetail = true;
        this.showReportDetailModal = true;
        this.currentReportDetail = null;
        
        axios.get(`/reports/${report.strId}`)
          .then(response => {
            console.log(':', response.data);
            if (response.data) {
              this.currentReportDetail = {
                strId: response.data.strId,
                sourceStrId: response.data.sourceStrId,
                sourceName: response.data.sourceName,
                sourceType: response.data.sourceType,
                generatedAt: response.data.generatedAt,
                isPublished: response.data.isPublished, 
                keywords: response.data.keywords,
                qrCodePath: response.data.qrCodePath,
                reportContent: this.filterReportContent(response.data.reportContent), // 
                difyMessageId: response.data.difyMessageId,
                usedData: response.data.usedData || []
              };
             
            }
          })
          .catch(error => {
            console.error(':', error);
            this.$message.error('');
            this.showReportDetailModal = false;
          })
          .finally(() => {
            this.loading.reportDetail = false;
          });
      },
  
      // 
      togglePublishStatus(report) {
         console.log(report.isPublished)
        const action = report.isPublished ? 'unpublish' : 'publish';
        const actionText = report.isPublished ? '' : '';
        const confirmMessage = report.isPublished 
          ? `${report.strId}\n`
          : `${report.strId}\nID`;
  
        if (!confirm(confirmMessage)) return;
  
        axios.post(`/reports/${report.strId}/${action}`)
          .then(() => {
            // 
            report.isPublished = !report.isPublished;
            this.showToast(`${actionText}`, 'success');
          })
          .catch(error => {
            console.error(`${actionText}:`, error);
            this.showToast(`${actionText}`, 'error');
          });
      },
  
      // 
      deleteReport(reportStrId) {
        const report = this.privateReports.find(r => r.strId === reportStrId);
        if (!report || !confirm(`${report.strId}\n`)) {
          return;
        }
  
        axios.delete(`/reports/${reportStrId}`)
          .then(() => {
            this.privateReports = this.privateReports.filter(r => r.strId !== reportStrId);
            this.filteredReports = this.filteredReports.filter(r => r.strId !== reportStrId);
            this.showToast('', 'success');
          })
          .catch(error => {
            console.error(':', error);
            this.showToast('', 'error');
          });
      },
  
      // 
      queryPublicReport() {
        if (!this.publicReportId) {
          this.showToast('ID', 'warning');
          return;
        }
  
        this.loading.publicReport = true;
        
        const reportStrId = this.publicReportId;
        
        axios.get(`http://47.92.234.121:8080/public/reports/${reportStrId}`)
          .then(response => {
            console.log(':', response);
            
            if (response.data) {
              // 
              this.publicReportDetail = {
                strId: response.data.strId,
                sourceStrId: response.data.sourceStrId,
                sourceName: response.data.sourceName,
                sourceType: response.data.sourceType,
                generatedAt: response.data.generatedAt,
                isPublished: response.data.isPublished, //  isPublished
                keywords: response.data.keywords,
                qrCodePath: response.data.qrCodePath,
                reportContent: this.filterReportContent(response.data.reportContent), // 
                message: response.data.message
              };
              this.$message.success('');
              console.log(':', this.publicReportDetail);
            } else {
              this.publicReportDetail = null;
              this.$message.warning('');
            }
          })
          .catch(error => {
            console.error(':', error);
            
            if (error.response) {
              switch (error.response.status) {
                case 404:
                  this.$message.error('');
                  break;
                case 500:
                  this.$message.error('');
                  break;
                default:
                  this.$message.error(`: ${error.response.data?.message || ''}`);
              }
            } 
            else {
              this.$message.error('');
            }
            
            this.publicReportDetail = null;
          })
          .finally(() => {
            this.loading.publicReport = false;
          });
      },
  
      // 
      validateGenerateForm() {
        return this.generateForm.sourceStrId.trim();
      },
  
      // 
      resetGenerateForm() {
        this.generateForm = {
          sourceStrId: ''
        };
      },
  
      //  - 
      formatDateTime(dateTimeData) {
        if (!dateTimeData) return '';
        
        try {
          let date;
          
          // 
          if (Array.isArray(dateTimeData)) {
            //  [2025, 11, 24, 14, 41, 23]
            const [year, month, day, hour = 0, minute = 0, second = 0] = dateTimeData;
            // 1JavaScript0
            date = new Date(year, month - 1, day, hour, minute, second);
          } else if (typeof dateTimeData === 'string') {
            // 
            date = new Date(dateTimeData);
          } else {
            console.warn(':', dateTimeData);
            return String(dateTimeData);
          }
          
          //  "YYYY-MM-DD HH:mm:ss"
          const formattedYear = date.getFullYear();
          const formattedMonth = String(date.getMonth() + 1).padStart(2, '0');
          const formattedDay = String(date.getDate()).padStart(2, '0');
          const formattedHours = String(date.getHours()).padStart(2, '0');
          const formattedMinutes = String(date.getMinutes()).padStart(2, '0');
          const formattedSeconds = String(date.getSeconds()).padStart(2, '0');
          
          return `${formattedYear}-${formattedMonth}-${formattedDay} ${formattedHours}:${formattedMinutes}:${formattedSeconds}`;
        } catch (error) {
          console.error(':', error);
          return Array.isArray(dateTimeData) ? dateTimeData.join('-') : String(dateTimeData);
        }
      },
  
      showToast(message, type = 'info') {
        const typeMap = {
          error: '',
          success: '',
          warning: '',
          info: ''
        };
        alert(`${typeMap[type] || ''}${message}`);
      },
      getQrCodeUrl(qrCodePath) {
    if (!qrCodePath) {
      return '';
    }
    
    // URL
    if (qrCodePath.startsWith('http')) {
      return qrCodePath;
    }
    
    // URL
    const baseUrl = 'http://47.92.234.121:8080';
    const normalizedBaseUrl = baseUrl.endsWith('/') ? baseUrl.slice(0, -1) : baseUrl;
    let normalizedPath = qrCodePath;
    
    // 
    if (!normalizedPath.startsWith('/')) {
      normalizedPath = '/' + normalizedPath;
    }
    
    return `${normalizedBaseUrl}${normalizedPath}`;
  },
  
  // ID
  copyReportId(reportId) {
    try {
      // API
      if (navigator.clipboard && window.isSecureContext) {
        navigator.clipboard.writeText(reportId).then(() => {
          this.showToast('ID', 'success');
        }).catch(err => {
          console.error(':', err);
          this.fallbackCopyReportId(reportId);
        });
      } else {
        // 
        this.fallbackCopyReportId(reportId);
      }
    } catch (error) {
      console.error('ID:', error);
      this.showToast('', 'error');
    }
  },
  
  // 
  fallbackCopyReportId(reportId) {
    try {
      // 
      const textArea = document.createElement('textarea');
      textArea.value = reportId;
      textArea.style.position = 'fixed';
      textArea.style.left = '-999999px';
      textArea.style.top = '-999999px';
      
      // DOM
      document.body.appendChild(textArea);
      
      // 
      textArea.focus();
      
      try {
        // 
        if (window.getSelection) {
          window.getSelection().removeAllRanges();
        } else if (document.selection) {
          document.selection.empty();
        }
        
        // 
        textArea.setSelectionRange(0, reportId.length);
        
        // 
        const successful = document.execCommand('copy');
        if (successful) {
          this.showToast('ID', 'success');
        } else {
          this.showToast('', 'error');
        }
      } catch (selectError) {
        console.error(':', selectError);
        // select()
        textArea.select();
        const successful = document.execCommand('copy');
        if (successful) {
          this.showToast('ID', 'success');
        } else {
          this.showToast('', 'error');
        }
      }
    } catch (err) {
      console.error(':', err);
      this.showToast('', 'error');
    } finally {
      // 
      const textArea = document.querySelector('textarea[style*="left: -999999px"]');
      if (textArea && textArea.parentNode) {
        document.body.removeChild(textArea);
      }
      
      // 
      try {
        document.activeElement.blur();
      } catch (e) {
        // 
      }
    }
  },
    }
  };
