<template>
  <header class="main-header">
    <div class="container">
    <div class="logo" @mouseenter="logoHover = true" @mouseleave="logoHover = false">
        <i class="el-icon-water-tap" :class="{ 'logo-icon-active': logoHover }"></i>
        <span class="logo-text" :class="{ 'logo-text-active': logoHover }">
          <span class="logo-text-highlight"></span><span class="logo-text-highlight"></span>
        </span>
      </div>
      
      <nav class="main-nav">
        <ul class="nav-links">
          <!--  -->
          <li class="nav-item">
            <router-link to="/" class="nav-link"></router-link>
          </li>
          <li class="nav-item">
            <a href="javascript:void(0)" class="nav-link" @click="handleNavigation('/water-sources')"></a>
          </li>
          <li class="nav-item">
            <a href="javascript:void(0)" class="nav-link" @click="handleNavigation('/report')"></a>
          </li>
          <li class="nav-item">
            <a href="javascript:void(0)" class="nav-link" @click="handleNavigation('/devices')"></a>
          
          <!--  -->
<!--  -->
<li class="nav-item" v-if="!isLoggedIn">
    <span class="nav-link btn login-btn" @click="handleNavigation('/auth')">
      / 
    </span>
</li>
<li class="nav-item" v-else>
  <span class="nav-link btn login-btn" @click="showLoggedInTip"></span>
</li>
          <li class="nav-item">
            <a href="javascript:void(0)" class="nav-link btn user-btn" @click="handleNavigation('/user-profile')">
              <i class="el-icon-user"></i> 
            </a>
          </li>
        </ul>
      </nav>
    </div>
  </header>
</template>

<script>
export default {
  data() {
    return {
      logoHover: false
    }
  },
  computed: {
    isLoggedIn() {
      // 
      return !!localStorage.getItem('token')
    }
  },
  methods: {
    handleNavigation(path) {
      // 
      if (this.isLoggedIn) {
        // 
        // 
         if (this.$route.path !== path)
        this.$router.push(path)
      } else {
        // 
        this.$message.warning('')
        this.$router.push('/auth')
      }
    },
      showLoggedInTip() {
    this.$message.info('')
  }
  }
}
</script>

<style scoped>
.main-header {
  background-color: rgb(255, 255, 255);
  box-shadow: 0 2px 15px rgba(22, 114, 255, 0.15);
  padding: 12px 0;
  position: sticky;  /*  */
  top: 0;
  z-index: 100;      /*  */
  transition: all 0.3s ease;
}

/*  */
.main-header.scrolled {
  padding: 8px 0;
  box-shadow: 0 4px 20px rgba(22, 114, 255, 0.2);
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
}

.logo {
  display: flex;
  align-items: center;
  gap: 10px;
  cursor: pointer;
  transition: transform 0.3s ease;
}

.logo:hover {
  transform: scale(1.03);
}

.logo i {
  font-size: 28px;
  color: #1672ff;
  transition: all 0.3s ease;
}

.logo-icon-active {
  transform: rotate(15deg);
  color: #0d5bde;
}

.logo-text {
  font-size: 20px;
  font-weight: 600;
  color: #1f2d3d;
  transition: all 0.3s ease;
  position: relative;
}

.logo-text-active {
  color: #0d5bde;
}

.logo-text::after {
  content: '';
  position: absolute;
  bottom: -3px;
  left: 0;
  width: 0;
  height: 2px;
  background-color: #1672ff;
  transition: width 0.3s ease;
}

.logo:hover .logo-text::after {
  width: 100%;
}

.nav-links {
  display: flex;
  list-style: none;
  gap: 30px;
  margin: 0;
  padding: 0;
  align-items: center; /*  */
}

.nav-item {
  position: relative;
}

.nav-link {
  color: #666;
  text-decoration: none;
  font-size: 16px;
  transition: all 0.3s ease;
  padding: 8px 0;
  position: relative;
  display: inline-block;
}

.nav-link::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 2px;
  background-color: #1672ff;
  transition: width 0.3s ease;
}

.nav-link:hover::after, 
.nav-link.router-link-exact-active::after {
  width: 100%;
}

.nav-link:hover, 
.nav-link.router-link-exact-active {
  color: #1672ff;
  transform: translateY(-2px);
}

/*  */
.btn {
  padding: 8px 16px;
  border-radius: 6px;
  text-decoration: none;
  font-size: 14px;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

/*  */
.btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255,255,255,0.2), transparent);
  transition: all 0.5s ease;
}

.btn:hover::before {
  left: 100%;
}

/*  */
.login-btn {
  border: 1px solid #1672ff;
  color: #1672ff;
}

.login-btn:hover {
  background-color: #f0f7ff;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(22, 114, 255, 0.2);
}

/*  */
.user-btn {
  background-color: #1672ff;
  color: white;
  display: flex;
  align-items: center;
  gap: 5px;
}

.user-btn:hover {
  background-color: #0d6efd;
  color: white; /* hover */
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(22, 114, 255, 0.3);
}

/*  */
@media (max-width: 768px) {
  .nav-links {
    gap: 15px;
  }
  
  .logo-text {
    font-size: 16px;
  }
  
  .nav-link {
    font-size: 14px;
  }
  
  .btn {
    padding: 6px 12px;
    font-size: 13px;
  }
}

</style>