<template>
  <div class="water-source-container">
      <!--  -->
    <MainHeader />
    <!--  -->
    <div class="bg-decoration"></div>
    
    <div class="content-wrapper">
      <!--  -->
      <div class="header-section">
        <div class="header-content">
          <div class="title-section">
            <h1 class="main-title"></h1>
            <p class="sub-title"></p>
          </div>

        </div>
        
        <!--  -  -->
        <div class="feature-cards-horizontal">
          <div class="feature-card" @click="handleAdd">
            <div class="feature-icon">
              <i class="el-icon-plus"></i>
            </div>
            <div class="feature-content">
              <h3></h3>
              <p></p>
            </div>
            <div class="feature-arrow">
              <i class="el-icon-arrow-right"></i>
            </div>
          </div>
          
        </div>
      </div>

      <!--  -->
      <div class="main-content">
        <!--  -->
        <div class="toolbar-section">
          <div class="search-box">
              <el-input
                v-model="searchId"
                placeholder="ID..."
                prefix-icon="el-icon-search"
                class="search-input"
                clearable
                @clear="handleSearchClear"
                @keyup.enter="handleSearchById"
              ></el-input>
              <el-button 
                type="primary" 
                @click="handleSearchById"
                :loading="searchLoading"
                class="search-btn"
              >
                
              </el-button>
            </div>
        </div>

        <!--  -->
        <div class="table-section">
          <el-card class="table-card" shadow="never">
            <div class="table-header">
              <h3 class="table-title"></h3>
              <div v-if="isSearching" class="search-status">
                <span class="search-tip"></span>
                <el-button 
                  type="text" 
                  @click="handleSearchClear"
                  class="clear-search-btn"
                >
                  
                </el-button>
              </div>
            </div>
            
            <el-table 
              :data="filteredWaterSources" 
              style="width: 100%;"
              :row-class-name="tableRowClassName"
              @row-mouse-enter="handleRowMouseEnter"
              @row-mouse-leave="handleRowMouseLeave"
              @row-click="handleRowClick"
              v-loading="loading"
              class="data-table clickable-row"
              empty-text=""
            >
              <el-table-column prop="strId" label="ID" width="120">
                <template slot-scope="scope">
                  <span class="id-text">{{ scope.row.strId }}</span>
                </template>
              </el-table-column>
              
              <el-table-column prop="sourceName" label="" min-width="180">
                <template slot-scope="scope">
                  <div class="source-name-cell">
                    <i class="el-icon-location source-icon"></i>
                    <span class="source-name">{{ scope.row.sourceName }}</span>
                  </div>
                </template>
              </el-table-column>
              
              <el-table-column prop="sourceType" label="" width="150">
                <template slot-scope="scope">
                  <el-tag 
                    :type="getSourceTypeTagType(scope.row.sourceType)"
                    class="source-type-tag"
                    effect="dark"
                  >
                    <i :class="getSourceTypeIcon(scope.row.sourceType)" class="tag-icon"></i>
                    {{ getSourceTypeName(scope.row.sourceType) }}
                  </el-tag>
                </template>
              </el-table-column>
              
              <el-table-column prop="description" label="" min-width="220">
                <template slot-scope="scope">
                  <span class="description-text">{{ scope.row.description || '' }}</span>
                </template>
              </el-table-column>
              
              <el-table-column prop="isActive" label="" width="100">
                <template slot-scope="scope">
                  <div class="status-cell">
                    <div :class="['status-dot', scope.row.isActive ? 'true' : 'false']"></div>
                    <span class="status-text">{{ scope.row.isActive ? '' : '' }}</span>
                  </div>
                </template>
              </el-table-column>
              
              <el-table-column prop="createdAt" label="" width="160">
                <template slot-scope="scope">
                  <span class="time-text">{{ formatTime(scope.row.createdAt) }}</span>
                </template>
              </el-table-column>
              
              <el-table-column label="" width="200" fixed="right">
                <template slot-scope="scope">
                  <div class="action-buttons">
                    <el-button 
                      @click="handleView(scope.row)" 
                      type="text" 
                      size="small"
                      class="btn-action view"
                    >
                      <i class="el-icon-view"></i>
                    </el-button>
                    <el-button 
                      @click="handleEdit(scope.row)" 
                      type="text" 
                      size="small"
                      class="btn-action edit"
                    >
                      <i class="el-icon-edit"></i>
                    </el-button>
                    <el-button 
                      @click="handleDelete(scope.row.strId)" 
                      type="text" 
                      size="small"
                      class="btn-action delete"
                    >
                      <i class="el-icon-delete"></i>
                    </el-button>
                  </div>
                </template>
              </el-table-column>
            </el-table>
          </el-card>
        </div>

        <!--  -->
        <div class="pagination-section">
          <el-pagination
            :current-page="currentPageIndex + 1"
            :page-size="pageSize"
            :total="totalRecords"
            layout="prev, pager, next, jumper"
            @current-change="handlePageChange"
            class="pagination-control"
            :disabled="isSearching"
          >
          </el-pagination>
          
          <div class="page-size-control">
            <span class="size-label">:</span>
            <el-select
              v-model="pageSize"
              @change="handlePageSizeChange"
              size="small"
              class="size-select"
              :disabled="isSearching"
            >
              <el-option label="5" value="5"></el-option>
              <el-option label="10" value="10"></el-option>
              <el-option label="20" value="20"></el-option>
              <el-option label="50" value="50"></el-option>
            </el-select>
            <span class="size-label"></span>
          </div>
        </div>
      </div>
    </div>

    <!--  -->
    <WaterSourceForm
      v-if="showAddForm || showEditForm"
      :visible="showAddForm || showEditForm"
      :form-data="currentWaterSource"
      :is-edit="showEditForm"
      @close="handleFormClose"
      @save="handleFormSave"
    ></WaterSourceForm>
  </div>
</template>

<script>
import axios from '../http'
import WaterSourceForm from '../components/WaterSourceForm.vue'
import MainHeader from '../components/MainHeader.vue'
export default {
  components: {
    WaterSourceForm,
     MainHeader  // 
  },
  data() {
    return {
      waterSources: [],
      filteredWaterSources: [],
      pageSize: 10,
      // 
      currentCursor: null, // 
      cursorHistory: [], // 
      hasPreviousPage: false,
      hasNextPage: false,
      showAddForm: false,
      showEditForm: false,
      currentWaterSource: {},
      // 
      currentPageIndex: 0,
      // ID
      searchId: '',
      // 
      loading: false,
      // 
      totalRecords: 0,
      // 
      isSearching: false,
      // 
      searchLoading: false
    }
  },
  computed: {
    // 
    currentRange() {
      const start = this.currentPageIndex * this.pageSize + 1
      const end = start + this.filteredWaterSources.length - 1
      return `${start}-${end}`
    }
  },
  watch: {
    searchId(val) {
      if (!val) {
        this.handleSearchClear()
      }
    }
  },
  created() {
    this.loadWaterSources()
  },
  methods: {
    // 
    async loadWaterSources() {
      this.loading = true
      try {
        const response = await axios.get('/water-sources', {
          params: {
            lastStrId: this.currentCursor,
            pageSize: this.pageSize
          }
        })
        
        // API
        let data = response.data
        // datadata
        if (data && data.data !== undefined) {
          data = data.data
        }
        
        this.waterSources = data || []
        this.filteredWaterSources = [...this.waterSources]
        
        // 
        this.hasNextPage = this.waterSources.length >= this.pageSize
        
        // 
        if (this.waterSources.length > 0) {
          this.totalRecords = (this.currentPageIndex + 1) * this.pageSize + (this.hasNextPage ? 1 : 0)
        }
        
      } catch (error) {
        this.$message.error('')
      } finally {
        this.loading = false
      }
    },

    // ID
    async handleSearchById() {
      if (!this.searchId.trim()) {
        this.$message.warning('ID')
        return
      }
      
      this.searchLoading = true
      this.loading = true
      this.isSearching = true
      
      try {
        const searchTerm = this.searchId.trim()
        
        // ID
        if (/^\d+$/.test(searchTerm)) {
          // ID
          try {
            // GET /water-sources/{sourceStrId}
            const response = await axios.get(`/water-sources/${searchTerm}`)
            
            console.log('ID:', response) // 
            
            // response.data.data
            let waterSource = null
            if (response.data) {
              // 
              if (response.data.data) {
                waterSource = response.data.data
              } else if (response.data.strId) {
                // 
                waterSource = response.data
              }
            }
            
            if (waterSource) {
              this.filteredWaterSources = [waterSource]
              this.$message.success('')
              return
            }
          } catch (idError) {
            console.log('ID', idError)
          }
        }
        
        // ID
        // 
        if (this.waterSources.length < this.pageSize) {
          await this.loadWaterSources()
        }
        
        // 
        const filtered = this.waterSources.filter(source => {
          return source.sourceName && source.sourceName.toLowerCase().includes(searchTerm.toLowerCase())
        })
        
        if (filtered.length > 0) {
          this.filteredWaterSources = filtered
          this.$message.success(` ${filtered.length} `)
        } else {
          this.filteredWaterSources = []
          this.$message.warning('')
          // 
          setTimeout(() => {
            this.handleSearchClear()
          }, 1500)
        }
      } catch (error) {
        console.error(':', error)
        this.filteredWaterSources = []
        this.$message.error('')
        
        // 
        setTimeout(() => {
          this.handleSearchClear()
        }, 1500)
      } finally {
        this.searchLoading = false
        this.loading = false
      }
    },

    // 
    handleSearchClear() {
      this.isSearching = false
      this.searchId = ''
      this.searchLoading = false
      this.filteredWaterSources = [...this.waterSources]
      // this.$message.info('')
    },

    // 
    refreshData() {
      this.currentCursor = null
      this.cursorHistory = []
      this.currentPageIndex = 0
      this.searchId = ''
      this.isSearching = false
      this.searchLoading = false
      this.loadWaterSources()
      this.$message.success('')
    },

    // 
    loadPreviousPage() {
      if (this.cursorHistory.length > 0) {
        // 
        this.currentCursor = this.cursorHistory.pop()
        this.currentPageIndex--
        this.loadWaterSources()
        
        // 
        this.hasPreviousPage = this.cursorHistory.length > 0
      }
    },

    // 
    loadNextPage() {
      if (this.waterSources.length > 0) {
        // ID
        const lastItem = this.waterSources[this.waterSources.length - 1]
        if (lastItem && lastItem.strId) {
          // 
          this.cursorHistory.push(this.currentCursor)
          // 
          this.currentCursor = lastItem.strId
          this.currentPageIndex++
          this.loadWaterSources()
          
          // 
          this.hasPreviousPage = true
        }
      }
    },

    // 
    handlePageChange(page) {
      if (this.isSearching) {
        this.$message.info('')
        return
      }
      
      if (page > this.currentPageIndex + 1) {
        // 
        this.loadNextPage()
      } else if (page < this.currentPageIndex + 1) {
        // 
        this.loadPreviousPage()
      }
    },

    // 
    handlePageSizeChange() {
      if (this.isSearching) {
        this.$message.info('')
        return
      }
      
      this.currentCursor = null
      this.cursorHistory = []
      this.currentPageIndex = 0
      this.loadWaterSources()
    },

    // 
    getSourceTypeName(type) {
      const types = {
        'GROUNDWATER': '',
        'SURFACE_WATER': '',
        'RESERVOIR': '',
        'RIVER': '',
        'LAKE': '',
        'DRINKING_WATER': '',
        'INDUSTRIAL_WATER': '',
        'AGRICULTURAL_WATER': '',
        'SEWAGE_TREATMENT_PLANT': '',
        'RAINWATER_COLLECTION': '',
        'WELL_WATER': '',
        'SPRING_WATER': ''

      }
      return types[type] || type
    },

    // 
    getSourceTypeTagType(type) {
      const typeMap = {
        'GROUNDWATER': 'info',
        'SURFACE_WATER': 'success',
        'RESERVOIR': 'warning',
        'RIVER': 'primary',
        'LAKE': 'danger',
        'DRINKING_WATER': 'success',
        'INDUSTRIAL_WATER': 'primary',
        'AGRICULTURAL_WATER': 'warning',
        'SEWAGE_TREATMENT_PLANT': 'info',
        'RAINWATER_COLLECTION': 'info',
        'WELL_WATER': 'primary',
        'SPRING_WATER': 'success'
      }
      return typeMap[type] || 'default'
    },

    // 
    getSourceTypeIcon(type) {
      const iconMap = {
        'GROUNDWATER': 'el-icon-bottom',
        'SURFACE_WATER': 'el-icon-top',
        'RESERVOIR': 'el-icon-data-board',
        'RIVER': 'el-icon-trend-charts',
        'LAKE': 'el-icon-picture',
        'DRINKING_WATER': 'el-icon-cup',
        'INDUSTRIAL_WATER': 'el-icon-suitcase',
        'AGRICULTURAL_WATER': 'el-icon-shopping-bag-1',
        'SEWAGE_TREATMENT_PLANT': 'el-icon-recycle',
        'RAINWATER_COLLECTION': 'el-icon-cloud-rain',
        'WELL_WATER': 'el-icon-bottom',
        'SPRING_WATER': 'el-icon-waterlily'
      }
      return iconMap[type] || 'el-icon-help'
    },

    //  -  "2025-11-06 16:04:11"
    formatTime(timeString) {
      if (!timeString) return '-'
      
      try {
        let date
        //  [2025, 11, 5, 21, 1, 35]
        if (Array.isArray(timeString)) {
          // 
          const [year, month, day, hour, minute, second] = timeString
          date = new Date(year, month - 1, day, hour, minute, second)
        } else if (typeof timeString === 'string' && timeString.includes('T')) {
          // ISO 8601 
          date = new Date(timeString)
        } else if (typeof timeString === 'number') {
          // 
          date = new Date(timeString)
        } else {
          // 
          date = new Date(timeString)
        }
        
        // 
        if (isNaN(date.getTime())) {
          return timeString // 
        }
        
        //  "YYYY-MM-DD HH:mm:ss"
        const year = date.getFullYear()
        const month = String(date.getMonth() + 1).padStart(2, '0')
        const day = String(date.getDate()).padStart(2, '0')
        const hours = String(date.getHours()).padStart(2, '0')
        const minutes = String(date.getMinutes()).padStart(2, '0')
        const seconds = String(date.getSeconds()).padStart(2, '0')
        
        return `${year}-${month}-${day} ${hours}:${minutes}:${seconds}`
      } catch (error) {
        console.error(':', error)
        return timeString // 
      }
    },

    // 
    tableRowClassName({ rowIndex }) {
      return rowIndex % 2 === 0 ? 'even-row' : 'odd-row'
    },

    handleRowMouseEnter(row, column, cell, event) {
      event.currentTarget.classList.add('hover-row')
      event.currentTarget.classList.add('shake-animation')
      // 
      setTimeout(() => {
        event.currentTarget.classList.remove('shake-animation')
      }, 500)
    },
    
    handleRowMouseLeave(row, column, cell, event) {
      event.currentTarget.classList.remove('hover-row')
    },
    
    // IDlocalStorage
    handleRowClick(row) {
      // IDlocalStorage
      localStorage.setItem('selectedWaterSourceId', row.strId);
      localStorage.setItem('selectedWaterSourceName', row.sourceName);
      // 
      this.$message.success(`: ${row.sourceName} (ID: ${row.strId})`);
    },

    // 
    handleAdd() {
      this.currentWaterSource = {
        sourceName: '',
        sourceType: '',
        description: '',
        active: true
      }
      this.showAddForm = true
      this.showEditForm = false
    },

    // 
    handleView(row) {
      this.currentWaterSource = { ...row }
      this.showAddForm = false
      this.showEditForm = true
    },

    // 
    handleEdit(row) {
      this.currentWaterSource = { ...row }
      this.showAddForm = false
      this.showEditForm = true
    },

    // 
    async handleDelete(strId) {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
        customClass: 'delete-confirm'
      }).then(async () => {
        try {
          await axios.delete(`/water-sources/${strId}`)
          this.$message.success('')
          // 
          this.currentCursor = null
          this.cursorHistory = []
          this.currentPageIndex = 0
          // 
          this.loadWaterSources()
        } catch (error) {
          this.$message.error('')
        }
      }).catch(() => {
        this.$message.info('')
      })
    },

    // 
    handleFormClose() {
      this.showAddForm = false
      this.showEditForm = false
      this.currentWaterSource = {}
    },

    // 
    async handleFormSave(data) {
      try {
        if (this.showEditForm) {
          await axios.put(`/water-sources/${data.strId}`, data)
          this.$message.success('')
        } else {
          await axios.post('/water-sources', data)
          this.$message.success('')
        }
        this.handleFormClose()
        // 
        this.currentCursor = null
        this.cursorHistory = []
        this.currentPageIndex = 0
        // 
        this.loadWaterSources()
      } catch (error) {
        this.$message.error(': ' + (error.response?.data?.message || error.message))
      }
    }
  }
}
</script>

<style scoped>
