  min-height: 90vh;
  background: linear-gradient(135deg, #f5f7fa 0%, #e4e7ed 100%);
  position: relative;
  overflow-x: hidden;
}

/*  */
.clickable-row {
  cursor: pointer;
}

.clickable-row .el-table__row:hover {
  background-color: #f5f7fa !important;
}

/*  */
