  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: 
    radial-gradient(circle at 10% 20%, rgba(120, 219, 226, 0.1) 0%, transparent 20%),
    radial-gradient(circle at 90% 80%, rgba(153, 246, 228, 0.1) 0%, transparent 20%);
  z-index: 0;
}

.content-wrapper {
  position: relative;
  z-index: 1;
  padding: 24px;
  max-width: 1400px;
  margin: 0 auto;
}

/*  */
.header-section {
  margin-bottom: 24px;
}

.header-content {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 24px;
}

.title-section .main-title {
  font-size: 32px;
  font-weight: 700;
  color: #1f2d3d;
  margin-bottom: 8px;
  background: linear-gradient(135deg, #1f2d3d 0%, #475669 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.title-section .sub-title {
  font-size: 16px;
  color: #8392a5;
  margin: 0;
}

/* .header-actions {
  display: flex;
  gap: 12px;
} */

.btn-user {
  background: rgba(255, 255, 255, 0.9);
