.feature-card:nth-child(2) .feature-icon {
  background: linear-gradient(135deg, #13ce66 0%, #10b857 100%);
}

.feature-content h3 {
  margin: 0 0 4px 0;
  font-size: 16px;
  font-weight: 600;
  color: #1f2d3d;
}

.feature-content p {
  margin: 0;
  font-size: 14px;
  color: #8392a5;
}

