}

/*  */
.main-content {
  background: rgba(255, 255, 255, 0.8);
  border-radius: 16px;
  padding: 24px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.5);
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.08);
}

/*  */
.toolbar-section {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
}

.search-box {
  display: flex;
  gap: 10px;
  align-items: center;
}

.search-input {
  width: 250px;
}

.search-input ::v-deep .el-input__inner {
  border-radius: 8px;
  border: 1px solid #e4e7ed;
  transition: all 0.3s ease;
}

.search-input ::v-deep .el-input__inner:focus {
  border-color: #20a0ff;
  box-shadow: 0 0 0 2px rgba(32, 160, 255, 0.2);
}

.search-btn {
  border-radius: 8px;
  transition: all 0.3s ease;
}

.search-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(32, 160, 255, 0.3);
}

/*  */
.table-section {
  margin-bottom: 24px;
}

.table-card {
  border-radius: 12px;
  border: none;
  overflow: hidden;
  background: transparent;
}

.table-card ::v-deep .el-card__body {
  padding: 0;
}

.table-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 24px;
  border-bottom: 1px solid #f0f2f5;
}

.table-title {
  margin: 0;
  font-size: 18px;
  font-weight: 600;
  color: #1f2d3d;
}

.search-status {
  display: flex;
  align-items: center;
  gap: 12px;
}

.search-tip {
