<template>
  <el-form ref="registerForm" :model="registerForm" :rules="rules" label-width="80px" class="register-form">
    <el-form-item label="" prop="username">
      <el-input v-model="registerForm.username"></el-input>
    </el-form-item>
    <el-form-item label="" prop="password">
      <el-input type="password" v-model="registerForm.password"></el-input>
    </el-form-item>

    <el-form-item>
      <el-button type="primary" @click="handleRegister"></el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import axios from '../http'

export default {
  data() {
    return {
      registerForm: {
        username: '',
        password: '',
      
      },
      rules: {
        username: [
          { required: true, message: '', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' }
        ],
      }
    }
  },
  methods: {
    handleRegister() {
      this.$refs.registerForm.validate(async (valid) => {
        if (valid) {
          try {
            await axios.post('/auth/register', this.registerForm)
            this.$emit('register-success')
          } catch (error) {
            this.$message.error(': ' + (error.response?.data?.message || error.message))
          }
        }
      })
    }
  }
}
</script>
<style scoped>
.register-form {
  /*  */
  width: 400px;
}

/*  */
.register-form ::v-deep .el-form-item__content {
  /*  */
  width: calc(100% - 80px); /* label-width */
}

.register-form ::v-deep .el-input {
  width: 100%; /*  */
}
</style>

