<template>
  <div class="water-data-upload">
    <div class="upload-content">
      <!--  -->
      <el-form 
        ref="uploadForm" 
        :model="form" 
        :rules="rules" 
        label-width="120px" 
        class="upload-form"
      >
        <el-form-item label="ID" prop="sourceStrId">
          <el-input
            v-model="form.sourceStrId"
            placeholder="ID"
            clearable
            class="form-input"
          >
            <template slot="prepend">
              <i class="el-icon-water-tap"></i>
            </template>
          </el-input>
        </el-form-item>

        <el-form-item label="" prop="turbidityValue">
          <el-input
            v-model.number="form.turbidityValue"
            placeholder=""
            type="number"
            step="0.01"
            clearable
            class="form-input"
          >
            <!-- <template slot="append">NTU</template> -->
            <template slot="prepend">
              <i class="el-icon-data-line"></i>
            </template>
          </el-input>
          <div class="form-tip">0-1000</div>
        </el-form-item>


        <el-form-item label="" prop="unit">
              <el-select
                v-model="form.unit"
                placeholder=""
                class="form-input"
              >
                <el-option
                  v-for="option in unitOptions"
                  :key="option.value"
                  :label="option.label"
                  :value="option.value"
                > </el-option>
              </el-select>
            </el-form-item>
        <el-form-item label="" prop="measuredAt">
          <el-date-picker
            v-model="form.measuredAt"
            type="datetime"
            placeholder=""
            format="yyyy-MM-dd HH:mm:ss"
            value-format="yyyy-MM-ddTHH:mm:ss"
            class="form-input"
            :editable="false"
          >
          </el-date-picker>
          <div class="form-tip"></div>
        </el-form-item>

        <!--  -->
        <el-card class="preview-card" shadow="never">
          <div slot="header" class="preview-header">
            <span></span>
          </div>
          <div class="preview-content">
            <div class="preview-item">
              <label>ID:</label>
              <span class="preview-value">{{ form.sourceStrId || '' }}</span>
            </div>
            <div class="preview-item">
              <label>:</label>
              <span class="preview-value" :class="getTurbidityClass(form.turbidityValue)">
                {{ form.turbidityValue || '' }} NTU
              </span>
            </div>
            <div class="preview-item">
              <label>:</label>
              <span class="preview-value">{{ formatPreviewTime(form.measuredAt) }}</span>
            </div>
          </div>
        </el-card>

        <!--  -->
        <div class="form-actions">
          <el-button 
            @click="handleCancel" 
            class="cancel-btn"
          >
            
          </el-button>
          <el-button 
            type="primary" 
            @click="handleSubmit" 
            :loading="loading"
            class="submit-btn"
          >
            <i class="el-icon-upload"></i>
            
          </el-button>
        </div>
      </el-form>

      <!--  -->
      <div class="history-section" v-if="showHistory">
        <el-divider content-position="left"></el-divider>
        <div class="history-toolbar">
          <el-input
            v-model="historySearch"
            placeholder="..."
            prefix-icon="el-icon-search"
            clearable
            style="width: 200px; margin-right: 10px;"
          ></el-input>
          <el-button 
            type="text" 
            icon="el-icon-refresh" 
            @click="loadHistoryData"
            :loading="historyLoading"
          >
            
          </el-button>
        </div>
        
        <el-table 
          :data="filteredHistoryData" 
          style="width: 100%; margin-top: 16px;"
          v-loading="historyLoading"
          empty-text=""
          class="history-table"
        >
          <el-table-column prop="timestamp" label="" width="180">
            <template slot-scope="scope">
              {{ formatTime(scope.row.timestamp) }}
            </template>
          </el-table-column>
          <el-table-column prop="turbidity" label="" width="120">
            <template slot-scope="scope">
              <span :class="getTurbidityClass(scope.row.turbidity)">
                {{ scope.row.turbidity }} NTU
              </span>
            </template>
          </el-table-column>
          <el-table-column prop="sourceStrId" label="ID"></el-table-column>
        </el-table>
      </div>
    </div>
  </div>
</template>

<script>
import axios from '../http'

export default {
  name: 'WaterDataUpload',
  data() {
    // 
    const validateTurbidity = (rule, value, callback) => {
      if (value === '' || value === null || value === undefined) {
        callback(new Error(''))
      } else if (isNaN(value)) {
        callback(new Error(''))
      } else if (value < 0 || value > 1000) {
        callback(new Error('0-1000'))
      } else {
        callback()
      }
    }

    return {
      loading: false,
      historyLoading: false,
      form: {
        sourceStrId: '',
        turbidityValue: null, //  turbidityValue
        unit: 'NTU', //  NTU
        measuredAt: '' // 
      },
      rules: {
        sourceStrId: [
          { required: true, message: 'ID', trigger: 'blur' }
        ],
        turbidityValue: [ //  turbidityValue
          { required: true, validator: validateTurbidity, trigger: 'blur' }
        ],
        unit: [ // 
          { required: true, message: '', trigger: 'change' }
        ],
        measuredAt: [ // 
          { required: true, message: '', trigger: 'change' }
        ]
      },
      // 
      showHistory: false,
      historyData: [],
      historySearch: '',
      filteredHistoryData: [],
       // 
      unitOptions: [
        { label: 'NTU', value: 'NTU' },
        { label: 'FTU', value: 'FTU' },
        { label: 'FAU', value: 'FAU' }
      ]
    }
  },
  computed: {
    // 
    computedHistoryData() {
      if (!this.historySearch) {
        return this.historyData
      }
      return this.historyData.filter(item => 
        item.sourceStrId.toLowerCase().includes(this.historySearch.toLowerCase()) ||
        item.turbidity.toString().includes(this.historySearch)
      )
    }
  },
  watch: {
    'form.sourceStrId'(newVal) {
      if (newVal) {
        this.showHistory = true
        this.loadHistoryData()
      } else {
        this.showHistory = false
        this.historyData = []
      }
    },
    historySearch() {
      this.filterHistoryData()
    }
  },
  methods: {
    // 
    handleSubmit() {
      this.$refs.uploadForm.validate(async (valid) => {
        if (valid) {
          this.loading = true
          try {
            // 
            const requestData = {
              sourceStrId: this.form.sourceStrId,
            turbidityValue: parseFloat(this.form.turbidityValue), //  turbidityValue
              unit: this.form.unit, // 
              measuredAt: this.form.measuredAt || this.getCurrentDateTime() // 
            }
            
            // 
            if (this.form.timestamp) {
              requestData.timestamp = this.form.timestamp
            }

            // 
            await axios.post('/water-data/upload', requestData)
            
            this.$message.success('')
            
            // 
            this.resetForm()
            
            // 
            this.loadHistoryData()
            
            // 
            this.$emit('success')
            
          } catch (error) {
            console.error(':', error)
            this.$message.error(': ' + (error.response?.data?.message || error.message))
          } finally {
            this.loading = false
          }
        } else {
          this.$message.warning('')
        }
      })
    },
//  ISO 
    getCurrentDateTime() {
      const now = new Date()
      return now.toISOString().slice(0, 19) // : "2023-11-06T10:30:00"
    },
    // 
    resetForm() {
      this.form.turbidityValue = null
      // this.form.unit = 'NTU'
      this.form.measuredAt = ''
      this.$refs.uploadForm.clearValidate()
    },

    // 
    handleCancel() {
      this.$emit('close')
    },

    // 
    async loadHistoryData() {
      if (!this.form.sourceStrId) {
        return
      }

      this.historyLoading = true
      try {
        // 
        const response = await axios.get(`/water-data/query/${this.form.sourceStrId}`)
        
        // 
        let data = response.data
        if (data && data.data !== undefined) {
          data = data.data
        }
        
        this.historyData = data || []
        this.filterHistoryData()
        
      } catch (error) {
        console.error(':', error)
        if (error.response && error.response.status !== 404) {
          this.$message.error('')
        }
      } finally {
        this.historyLoading = false
      }
    },

    // 
    filterHistoryData() {
      if (!this.historySearch) {
        this.filteredHistoryData = [...this.historyData]
      } else {
        this.filteredHistoryData = this.historyData.filter(item => 
          item.sourceStrId.toLowerCase().includes(this.historySearch.toLowerCase()) ||
          item.turbidity.toString().includes(this.historySearch)
        )
      }
    },

    // 
    getTurbidityClass(turbidity) {
      if (!turbidity && turbidity !== 0) return ''
      
      if (turbidity < 1) {
        return 'turbidity-excellent'
      } else if (turbidity < 5) {
        return 'turbidity-good'
      } else if (turbidity < 10) {
        return 'turbidity-fair'
      } else {
        return 'turbidity-poor'
      }
    },

    // 
    formatPreviewTime(timestamp) {
      if (!timestamp) {
        return ''
      }
      return this.formatTime(timestamp)
    },

    // 
    formatTime(timeString) {
      if (!timeString) return '-'
      
      try {
        const date = new Date(timeString)
        if (isNaN(date.getTime())) {
          return timeString
        }
        
        const year = date.getFullYear()
        const month = String(date.getMonth() + 1).padStart(2, '0')
        const day = String(date.getDate()).padStart(2, '0')
        const hours = String(date.getHours()).padStart(2, '0')
        const minutes = String(date.getMinutes()).padStart(2, '0')
        const seconds = String(date.getSeconds()).padStart(2, '0')
        
        return `${year}-${month}-${day} ${hours}:${minutes}:${seconds}`
      } catch (error) {
        return timeString
      }
    }
  }
}
</script>

<style scoped>
.water-data-upload {
  padding: 0;
}

.upload-content {
  max-width: 100%;
}

.upload-form {
  padding: 20px 24px;
}

.form-input {
  width: 100%;
}

/*  */
.form-input .el-select {
  width: 100%;
}

.form-tip {
  font-size: 12px;
  color: #909399;
  margin-top: 4px;
}

/*  */
.preview-card {
  margin: 20px 0;
  border: 1px solid #e4e7ed;
  border-radius: 8px;
}

.preview-header {
  font-weight: 600;
  color: #1f2d3d;
  padding: 12px 20px;
  border-bottom: 1px solid #f0f2f5;
}

.preview-content {
  padding: 16px 20px;
}

.preview-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 12px;
  padding: 8px 0;
}

.preview-item:last-child {
  margin-bottom: 0;
}

.preview-item label {
  font-weight: 500;
  color: #606266;
}

.preview-value {
  font-weight: 600;
  color: #1f2d3d;
}

/*  */
.turbidity-excellent {
  color: #13ce66;
}

.turbidity-good {
  color: #20a0ff;
}

.turbidity-fair {
  color: #ffba00;
}

.turbidity-poor {
  color: #ff4949;
}

/*  */
.form-actions {
  display: flex;
  justify-content: flex-end;
  gap: 12px;
  margin-top: 24px;
  padding-top: 16px;
  border-top: 1px solid #f0f2f5;
}

.cancel-btn {
  width: 100px;
}

.submit-btn {
  width: 140px;
}

/*  */
.history-section {
  margin-top: 24px;
  padding: 0 24px 24px;
}

.history-toolbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
}

.history-table {
  border-radius: 6px;
  overflow: hidden;
}

.history-table ::v-deep .el-table__row {
  transition: all 0.3s ease;
}

.history-table ::v-deep .el-table__row:hover {
  background-color: #f5f7fa;
}

/*  */
@media (max-width: 768px) {
  .upload-form {
    padding: 16px;
  }
  
  .form-actions {
    flex-direction: column;
  }
  
  .cancel-btn,
  .submit-btn {
    width: 100%;
  }
  
  .history-section {
    padding: 0 16px 16px;
  }
  
  .history-toolbar {
    flex-direction: column;
    gap: 12px;
    align-items: stretch;
  }
}
</style>