<template>
  <div class="auth-container">
    <!--  -->
    <div class="particles-bg" id="particles-js"></div>
    <!--  -->
    <div class="glass-card">
      <!--  -->
      <div class="card-decoration">
        <div class="decoration-circle circle-1"></div>
        <div class="decoration-circle circle-2"></div>
        <div class="decoration-circle circle-3"></div>
      </div>
      
      <!--  -->
      <div class="card-content">
        <!--  -->
        <div class="system-header">
          <div class="logo-container">
            <i class="el-icon-water-tap logo-icon"></i>
          </div>
          <h1 class="system-title"></h1>
          <p class="system-subtitle">    </p>
        </div>

        <!--  -  -->
        <div class="auth-tabs">
          <div class="tabs-header">
            <div 
              class="tab-item" 
              :class="{ 'active': activeTab === 'login' }"
              @click="activeTab = 'login'"
            >
              <span class="tab-label"></span>
            </div>
            <div class="tab-divider">|</div>
            <div 
              class="tab-item" 
              :class="{ 'active': activeTab === 'register' }"
              @click="activeTab = 'register'"
            >
              <span class="tab-label"></span>
            </div>
          </div>
          
          <div class="tabs-content">
            <LoginComponent 
              v-if="activeTab === 'login'" 
              @login-success="handleLoginSuccess" 
            />
            <RegisterComponent 
              v-if="activeTab === 'register'" 
              @register-success="handleRegisterSuccess" 
            />
          </div>
        </div>
      </div>
    </div>

    <!--  -->
    <div class="water-wave"></div>
  </div>
</template>

<script>
import LoginComponent from '../components/AppLogin.vue'
import RegisterComponent from '../components/AppRegister.vue'
export default {
  components: {
    LoginComponent,
    RegisterComponent
  },
  data() {
    return {
      activeTab: 'login'
    }
  },
  mounted() {
    this.initParticles()
      // URL
       const type = this.$route.query.type
    if (type === 'register') {
      this.activeTab = 'register'
    }
  },
  methods: {
    handleLoginSuccess() {
      // 
      document.querySelector('.glass-card').classList.add('success-animation')
      setTimeout(() => {
        this.$router.push('/')
      }, 800)
    },
    handleRegisterSuccess() {
      this.activeTab = 'login'
      this.$message.success('')
    },
    initParticles() {
      // 
      if (typeof window !== 'undefined') {
        const canvas = document.createElement('canvas')
        const ctx = canvas.getContext('2d')
        const container = document.getElementById('particles-js')
        
        if (container && ctx) {
          container.appendChild(canvas)
          this.setupParticles(canvas, ctx)
        }
      }
    },
    setupParticles(canvas, ctx) {
      // 
      const resizeCanvas = () => {
        canvas.width = window.innerWidth
        canvas.height = window.innerHeight
      }
      resizeCanvas()
      window.addEventListener('resize', resizeCanvas)

      // 
      const particles = []
      const particleCount = 30

      // 
      for (let i = 0; i < particleCount; i++) {
        particles.push({
          x: Math.random() * canvas.width,
          y: Math.random() * canvas.height,
          size: Math.random() * 3 + 1,
          speedX: Math.random() * 1 - 0.5,
          speedY: Math.random() * 1 - 0.5,
          opacity: Math.random() * 0.5 + 0.2
        })
      }

      // 
      const animate = () => {
        ctx.clearRect(0, 0, canvas.width, canvas.height)
        
        // 
        particles.forEach(particle => {
          particle.x += particle.speedX
          particle.y += particle.speedY
          
          // 
          if (particle.x < 0 || particle.x > canvas.width) particle.speedX *= -1
          if (particle.y < 0 || particle.y > canvas.height) particle.speedY *= -1
          
          // 
          ctx.beginPath()
          ctx.arc(particle.x, particle.y, particle.size, 0, Math.PI * 2)
          ctx.fillStyle = `rgba(255, 255, 255, ${particle.opacity})`
          ctx.fill()
        })
        
        requestAnimationFrame(animate)
      }
      
      animate()
    }
  }
}
</script>

