<template>
  <div class="user-settings-container">
    <MainHeader />
    
    <div class="settings-layout">
      <!--  -->
      <div class="sidebar-nav">
        <div class="user-profile-card">
          <div class="avatar-container">
            <img 
              v-if="userInfo && userInfo.avatar && !avatarLoadError" 
              :src="getAvatarUrl(userInfo.avatar)" 
              alt="" 
              class="avatar-image"
              @error="handleAvatarError"
              @load="handleAvatarLoad"
            />
            <div v-else class="avatar-default">
              <i class="el-icon-user avatar-icon"></i>
            </div>
          </div>
          <div class="user-info">
            <h2 class="username">{{ (userInfo && userInfo.username) || '' }}</h2>
            <div class="status-indicator">
              <div class="status-dot active"></div>
              <span class="status-text"></span>
            </div>
          </div>
        </div>
        <el-menu
          :default-active="activeMenu"
          class="sidebar-menu"
          @select="handleMenuSelect"
        >
          <el-menu-item index="profile">
            <i class="el-icon-user"></i>
            <span></span>
          </el-menu-item>
          <el-menu-item index="avatar">
            <i class="el-icon-camera"></i>
            <span></span>
          </el-menu-item>
        </el-menu>
      </div>
      <!--  -->
      <div class="content-area">
        <!--  -->
        <div v-if="activeMenu === 'profile'" class="content-section">
          <h3 class="section-title">
            <i class="el-icon-user"></i>
            
          </h3>
          
          <div class="profile-info">
            <div class="info-card">
              <div class="info-content">
                <div class="info-item">
                  <span class="info-label"></span>
                  <span class="info-value">{{ userInfo.username || '' }}</span>
                  <el-button 
                    type="text" 
                    class="edit-btn"
                    @click="showUsernameDialog"
                  >
                    
                  </el-button>
                </div>
                <div class="info-item">
                  <span class="info-label">ID</span>
                  <span class="info-value">{{ userInfo.strId || '' }}</span>
                </div>
                <div class="info-item">
                  <span class="info-label"></span>
                  <span class="info-value">{{ userInfo.email || '' }}</span>
                  <el-button 
                    type="text" 
                    class="edit-btn"
                    @click="showEmailDialog"
                  >
                    
                  </el-button>
                </div>
                <div class="info-item">
                  <span class="info-label"></span>
                  <span class="info-value">******</span>
                  <el-button 
                    type="text" 
                    class="edit-btn"
                    @click="showPasswordDialog"
                  >
                    
                  </el-button>
                </div>
              </div>
            </div>
            <!--  -  -->
            <div class="info-card account-actions-card">
    
              <div class="info-content account-actions">
                <el-button 
                  @click="handleLogout" 
                  type="warning"
                  class="action-btn"
                >
                  <i class="el-icon-switch-button"></i>
                  
                </el-button>
                <el-button 
                  @click="handleDeleteAccount" 
                  type="danger"
                  class="action-btn"
                >
                  <i class="el-icon-delete"></i>
                  
                </el-button>
              </div>
            </div>
          </div>
        </div>
        <!--  -->
        <div v-if="activeMenu === 'avatar'" class="content-section">
          <h3 class="section-title">
            <i class="el-icon-camera"></i>
            
          </h3>
          <div class="avatar-upload-section">
            <div class="current-avatar">
              <h4></h4>
              <div class="avatar-preview">
                <img 
                  v-if="userInfo && userInfo.avatar && !avatarLoadError" 
                  :src="getAvatarUrl(userInfo.avatar)" 
                  alt=""
                  @error="handleAvatarError"
                  @load="handleAvatarLoad"
                />
                <div v-else class="avatar-placeholder">
                  <i class="el-icon-user"></i>
                </div>
              </div>
              <p v-if="avatarLoadError" class="avatar-error-tip"></p>
            </div>
            
            <div class="upload-area">
              <h4></h4>
              <el-upload
                class="avatar-uploader"
                action="#"
                :show-file-list="false"
                :before-upload="beforeAvatarUpload"
                :http-request="customUpload"
              >
                <div class="upload-trigger">
                  <i class="el-icon-upload"></i>
                  <div class="upload-text">
                    <p></p>
                    <p class="upload-tips"> JPGPNG  2MB</p>
                  </div>
                </div>
              </el-upload>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="usernameDialogVisible"
      width="400px"
    >
      <el-form :model="usernameForm" :rules="usernameRules" ref="usernameForm">
        <el-form-item label="" prop="username">
          <el-input v-model="usernameForm.username" placeholder=""></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="usernameDialogVisible = false"></el-button>
        <el-button type="primary" @click="updateUsername" :loading="usernameLoading"></el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="emailDialogVisible"
      width="400px"
    >
      <el-form :model="emailForm" :rules="emailRules" ref="emailForm">
        <el-form-item label="" prop="email">
          <el-input v-model="emailForm.email" placeholder=""></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="emailDialogVisible = false"></el-button>
        <el-button type="primary" @click="updateEmail" :loading="emailLoading"></el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="passwordDialogVisible"
      width="400px"
    >
      <el-form :model="passwordForm" :rules="passwordRules" ref="passwordForm">
        <el-form-item label="" prop="newPassword">
          <el-input 
            type="password"
            v-model="passwordForm.newPassword" 
            placeholder=""
            show-password
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="confirmPassword">
          <el-input 
            type="password"
            v-model="passwordForm.confirmPassword" 
            placeholder=""
            show-password
          ></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="passwordDialogVisible = false"></el-button>
        <el-button type="primary" @click="updatePassword" :loading="passwordLoading"></el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="deleteAccountDialogVisible"
      width="400px"
      :close-on-click-modal="false"
    >
      <div class="delete-warning">
        <div class="warning-icon">
          <i class="el-icon-warning-outline"></i>
        </div>
        <div class="warning-content">
          <p class="warning-title"></p>
          <ul class="warning-list">
            <li> </li>
            <li> </li>
            <li> </li>
          </ul>
          <p class="confirm-text">""</p>
          <el-input 
            v-model="deleteConfirmText" 
            placeholder=""
            class="confirm-input"
          ></el-input>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="deleteAccountDialogVisible = false"></el-button>
        <el-button 
          type="danger" 
          @click="confirmDeleteAccount"
          :disabled="deleteConfirmText !== ''"
        >
          
        </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import axios from '../http'
import MainHeader from './MainHeader.vue'
export default {
  name: 'UserSettings',
    components: {
    MainHeader
  },
  data() {
    const validateConfirmPassword = (rule, value, callback) => {
      if (value !== this.passwordForm.newPassword) {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    const validateEmail = (rule, value, callback) => {
      if (value && !/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(value)) {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    return {
      activeMenu: 'profile',
      userInfo: {},
      avatarTimestamp: Date.now(),
      showApiKey: false,
      avatarLoadError: false,
      
      // 
      usernameDialogVisible: false,
      emailDialogVisible: false,
      passwordDialogVisible: false,
      deleteAccountDialogVisible: false, // 
      deleteConfirmText: '', // 
      
      // 
      usernameForm: {
        username: ''
      },
      usernameLoading: false,
      usernameRules: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 20, message: ' 2  20 ', trigger: 'blur' }
        ]
      },
      
      // 
      emailForm: {
        email: ''
      },
      emailLoading: false,
      emailRules: {
        email: [
          { required: true, message: '', trigger: 'blur' },
          { validator: validateEmail, trigger: 'blur' }
        ]
      },
      
      // 
      passwordForm: {
        newPassword: '',
        confirmPassword: ''
      },
      passwordLoading: false,
      passwordRules: {
        newPassword: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, message: ' 6 ', trigger: 'blur' }
        ],
        confirmPassword: [
          { required: true, message: '', trigger: 'blur' },
          { validator: validateConfirmPassword, trigger: 'blur' }
        ]
      }
    }
  },
  computed: {
  },
  created() {
    this.loadUserInfo()
  },
  methods: {
    // URL
getAvatarUrl(avatarUrl) {
  if (!avatarUrl) {
    this.avatarLoadError = true;
    return '';
  }
  
  try {
    // URL
    if (avatarUrl.startsWith('http')) {
      return avatarUrl;
    }
    
    //const baseUrl = 'http://47.92.234.121:8080';
    const baseUrl = '';
    const normalizedBaseUrl = baseUrl.endsWith('/') ? baseUrl.slice(0, -1) : baseUrl;
    
    // 
    let normalizedAvatarUrl = avatarUrl;
    if (!normalizedAvatarUrl.startsWith('/')) {
      normalizedAvatarUrl = '/' + normalizedAvatarUrl;
    }
    
    // URL
    return `${normalizedBaseUrl}${normalizedAvatarUrl}`;
  } catch (error) {
    console.error('URL:', error);
    this.avatarLoadError = true;
    return '';
  }
},
    // 
    handleAvatarLoad() {
      this.avatarLoadError = false;
      console.log('');
    },
    // 
    handleAvatarError(event) {
      console.error(':', event);
      this.avatarLoadError = true;
      
      // 
      console.log('URL:', event.target.src);
      console.log(':', this.userInfo.avatar);
    },
    // 
    async loadUserInfo() {
      try {
        // 
        this.avatarLoadError = false;
        
        const response = await axios.get('/auth/me')
        this.userInfo = response.data || {}
        console.info(":", this.userInfo)
        
        // 
        if (this.userInfo.avatar) {
          console.log(':', this.userInfo.avatar);
          console.log('URL:', this.getAvatarUrl(this.userInfo.avatar));
        }
        
        this.avatarTimestamp = Date.now()
      } catch (error) {
        console.error(':', error)
        this.userInfo = {}
        this.$message.error('')
      }
    },
    // 
    formatDate(date) {
      if (!date) return ''
      return new Date(date).toLocaleString('zh-CN')
    },
    // 
    handleMenuSelect(index) {
      this.activeMenu = index
    },
    // 
    showUsernameDialog() {
      this.usernameForm.username = this.userInfo.username || ''
      this.usernameDialogVisible = true
      this.$nextTick(() => {
        this.$refs.usernameForm && this.$refs.usernameForm.clearValidate()
      })
    },
    // 
    showEmailDialog() {
      this.emailForm.email = this.userInfo.email || ''
      this.emailDialogVisible = true
      this.$nextTick(() => {
        this.$refs.emailForm && this.$refs.emailForm.clearValidate()
      })
    },
    // 
    showPasswordDialog() {
      this.passwordForm.newPassword = ''
      this.passwordForm.confirmPassword = ''
      this.passwordDialogVisible = true
      this.$nextTick(() => {
        this.$refs.passwordForm && this.$refs.passwordForm.clearValidate()
      })
    },
    // 
    async updateUsername() {
      try {
        await this.$refs.usernameForm.validate()
        this.usernameLoading = true
        
        const updateData = {
          username: this.usernameForm.username
        }
        
        await axios.post('/auth/update', updateData)
        this.$message.success('')
        this.usernameDialogVisible = false
        this.loadUserInfo()
      } catch (error) {
        if (error !== 'cancel') {
          console.error(':', error)
          this.$message.error('')
        }
      } finally {
        this.usernameLoading = false
      }
    },
    // 
    async updateEmail() {
      try {
        await this.$refs.emailForm.validate()
        this.emailLoading = true
        
        const updateData = {
          email: this.emailForm.email
        }
        
        await axios.post('/auth/update', updateData)
        this.$message.success('')
        this.emailDialogVisible = false
        this.loadUserInfo()
      } catch (error) {
        if (error !== 'cancel') {
          console.error(':', error)
          this.$message.error('')
        }
      } finally {
        this.emailLoading = false
      }
    },
    // 
    async updatePassword() {
      try {
        await this.$refs.passwordForm.validate()
        this.passwordLoading = true
        
        const updateData = {
          password: this.passwordForm.newPassword
        }
        
        await axios.post('/auth/update', updateData)
        this.$message.success('')
        this.passwordDialogVisible = false
        this.resetPasswordForm()
      } catch (error) {
        if (error !== 'cancel') {
          console.error(':', error)
          this.$message.error('')
        }
      } finally {
        this.passwordLoading = false
      }
    },
    // 
    resetPasswordForm() {
      this.passwordForm.newPassword = ''
      this.passwordForm.confirmPassword = ''
    },
    // 
    async customUpload(file) {
      try {
        const formData = new FormData()
        formData.append('file', file.file)
        
        this.$message.info('...')
        
        const response = await axios.post('/auth/updateavatar', formData, {
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        })
        
        if (response.data && response.data.code === 200) {
          this.$message.success('')
          this.avatarTimestamp = Date.now()
          this.avatarLoadError = false; // 
          this.loadUserInfo()
        } 
      } catch (error) {
        console.error(':', error)
        if (error.response && error.response.status === 403) {
          this.$message.error('')
        } else {
          this.$message.error('')
        }
      }
    },
    // 
    beforeAvatarUpload(file) {
      const token = localStorage.getItem('token')
      if (!token) {
        this.$message.error('')
        return false
      }
      const isImage = file.type === 'image/jpeg' || 
                     file.type === 'image/png' || 
                     file.type === 'image/gif' ||
                     file.type === 'image/webp'
      const isLt2M = file.size / 1024 / 1024 < 2
      if (!isImage) {
        this.$message.error(' JPG/PNG/GIF/WEBP !')
        return false
      }
      if (!isLt2M) {
        this.$message.error(' 2MB!')
        return false
      }
      return true
    },
    // 
    goBack() {
      if (window.history.length > 1) {
        this.$router.go(-1)
      } else {
        this.$router.push('/water-sources')
      }
    },
    // 
    async handleLogout() {
      try {
        await this.$confirm('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        })
        
        await axios.delete('/auth/logout')
        localStorage.removeItem('token')
        this.$router.push('/')
        this.$message.success('')
      } catch (error) {
        if (error !== 'cancel') {
          this.$message.error('')
        }
      }
    },
    // 
    handleDeleteAccount() {
      this.deleteConfirmText = ''
      this.deleteAccountDialogVisible = true
    },
    // 
    async confirmDeleteAccount() {
      try {
        await axios.delete('/auth/me')
        localStorage.removeItem('token')
        this.$router.push('/')
        this.$message.success('')
        this.deleteAccountDialogVisible = false
      } catch (error) {
        this.$message.error('')
      }
    }
  }
}
</script>
<style scoped>
.user-settings-container {
  min-height: 100vh;
  background: #fffefe;
  position: relative;
  overflow-x: hidden;
  box-sizing: border-box;
}
