}
.avatar-placeholder {
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #409EFF 0%, #67C23A 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 40px;
}
.user-info .username {
  margin: 0 0 10px 0;
