import Vue from 'vue'
import Router from 'vue-router'
import HomePage from './views/HomePage.vue'
import AuthView from './views/AuthView.vue'
import WaterSourceView from './views/WaterSourceView.vue'
import UserProfile from './components/UserProfile.vue'
import WaterDataUpload from './components/WaterDataUpload.vue'
import AppReport from './views/AppReport.vue'
import DevicesView from './views/DevicesView.vue' // 
Vue.use(Router)

const router = new Router({
  mode: 'history', // history
  routes: [
    {
      path: '/',
      name: 'home',
      component: HomePage  // 
    },
    {
      path: '/auth',
      name: 'auth',
      component: AuthView
    },
    {
      path: '/water-sources',
      name: 'waterSources',
      component: WaterSourceView,
      meta: { requiresAuth: true }
    },
    {
      path: '/user-profile',
      name: 'userProfile',
      component: UserProfile,
      meta: { requiresAuth: true }
    },
  {
  path: '/water-data',
  name: 'UploadWaterData',
  component: WaterDataUpload,
  meta: { requiresAuth: true }
}, 
{
    path: '/report', // Report
    name: 'Report',
    component: AppReport, // Report
    meta: { requiresAuth: true }  // 
  },
  {
    path: '/devices',  // 
    name: 'devices',
    component: DevicesView,  // 
    meta: { requiresAuth: true }
  }
  ]
})

// 
router.beforeEach((to, from, next) => {
  const requiresAuth = to.matched.some(record => record.meta.requiresAuth)
  const isLoggedIn = !!localStorage.getItem('token')

  if (requiresAuth && !isLoggedIn) {
    next('/auth') // 
  } else if (to.path === '/auth' && isLoggedIn) {
    next('/') // 
  } else {
    next()
  }
})


export default router